/* #region page */
.page {
  font-family: "Manrope", sans-serif;
  line-height: 140%;
  font-size: 16px;
  color: #1b2129;
  scroll-behavior: smooth !important;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page .page__body {
  margin: 0;
}

.page__menu {
  position: fixed;
  transform: translateX(100%);
  top: 0;
  left: 0;
  right: 0;
  pointer-events: none;
}

.page__menu:target {
  transform: translateX(0);
  pointer-events: all;
}

/* #endregion */
/* #region header */
.header {
  box-sizing: border-box;
  height: 100vh;
  background-size: cover;
  background-position: center;
  background-image: url(../images/header-bg-mobile.jpg);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: #fff;
}

.header__title {
  margin: 0;
  font-size: 36px;
  font-style: normal;
  font-weight: 700;
  line-height: 100%;
  padding: 40px 20px;
}
@media (min-width: 774px) {
  .header__title {
    padding: 64px 72px;
    font-size: 48px;
  }
}
@media (min-width: 1260px) {
  .header__title {
    padding: 64px 120px;
    font-size: 56px;
  }
}

.header__bottom {
  padding: 0 20px 40px;
}
/* #endregion */
/* #region top-bar */
.top-bar {
  padding-block: 24px;
  padding-inline: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 774px) {
  .top-bar {
    padding: 36px 72px;
  }
}
@media (min-width: 1260px) {
  .top-bar {
    padding: 36px 120px;
  }
}

.top-bar__icons {
  display: flex;
  gap: 24px;
  justify-content: center;
}

.top-bar__logo {
  display: flex;
}
@media (min-width: 774px) {
  .top-bar__logo {
    width: 228px;
    height: 24px;
  }
}

/* #endregion */
/* #region menu */
.menu {
  box-sizing: border-box;
  height: 100vh;
  width: auto;
  background-color: #f9f9f9;
  transform: translateX(-100%);
  font-weight: 700;
  transition: 0.2s;
}

.menu__content {
  padding-inline: 20px;
}
@media (min-width: 774px) {
  .menu__content {
    padding-inline: 72px;
  }
}
@media (min-width: 1260px) {
  .menu__content {
    padding-inline: 120px;
  }
}

.menu__top {
  margin-bottom: 32px;
  position: sticky;
  top: 0;
  z-index: 1;
}

.menu__nav {
  margin-bottom: 48px;
}

.menu__phone-number {
  display: block;
  margin: 48px 0 16px;
  line-height: 22px;
  font-size: 16px;
  letter-spacing: 2px;
  color: inherit;
  text-decoration: none;
}

.menu__call-to-order {
  font-size: 16px;
  letter-spacing: 2px;
  padding-bottom: 3px;
  border-bottom: 1px solid #1b2129;
  color: inherit;
  text-decoration: none;
}

/* #endregion */
/* #region nav */
.nav__link {
  color: inherit;
  text-decoration: none;
  font-size: 22px;
  line-height: 22px;
}

.nav__link:hover {
  border-bottom: 1px solid #1b2129;
  transition: 0.2s;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 32px;
}
/* #endregion */
/* #region icon */
.icon {
  display: block;
  background-repeat: no-repeat;
  height: 24px;
  width: 24px;
}
@media (min-width:1260px) {
  .icon {
    height: 32px;
    width: 32px;
  }
}

.icon--phone {
  background-image: url(../images/icons/phone.svg);
  position: relative;
}

.icon--phone:hover {
  transform: scale(1.25);
  transition: 0.2s;
}
@media (min-width:1260px) {
  .icon--phone::before {
    opacity: 0;
    color: #1b2129;
    text-align: right;
    font-size: 12px;
    font-style: normal;
    font-weight: 700;
    width: 159px;
    line-height: 16px;
    letter-spacing: 2px;
    text-transform: uppercase;
    content: "+1 234 5555-55-55";
  }

  .icon--phone:hover::before {
    opacity: 1;
    transition: opacity 0.4s ease;
    left: -450%;
    display: flex;
    font-size: 12px;
    letter-spacing: 2px;
    line-height: 16px;
    color: #1b2129;
    width: 159px;
    height: 16px;
    margin-top: 5px;
    position: absolute;
  }
}

.icon__tooltip {
  display: flex;
  width: 139px;
  position: absolute;
  color: #010810;
}

.icon--menu {
  background-image: url(../images/icons/burger-menu.svg);
}

.icon--menu:hover {
  transform: scale(1.25);
  transition: 0.2s;
}
@media (min-width:1260px) {
  .icon--menu:hover {
    background-image: url(../images/icons/burger-menu-1260.svg);
  }
}

.icon--close {
  background-image: url(../images/icons/close.svg);
}

.icon--close:hover {
  transform: scale(1.25);
  transition: 0.2s;
}

/* #endregion */
/* #region footer */
.footer {
  z-index: -1;
  margin: 48px 20px;
}
@media (min-width: 774px) {
  .footer {
    margin: 80px 72px;
  }
}
@media (min-width: 1260px) {
  .footer {
    width: 411px;
    margin: 0;
    margin-left: 111px;
  }
}

.footer__title {
  color: #9393a3;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
  letter-spacing: 0.15px;
}

.footer__content {
  color: #191919;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
  width: 100%;
  letter-spacing: 0.15px;
}

.footer__content:hover {
  color: #f98921;
  transition: 0.2s;
}

.footer__links {
  text-decoration: none;
}

@media (min-width: 1260px) {
  .footer-1260px {
    display: flex;
    justify-content: center;
  }

  .footer {
    z-index: 1;
    padding-top: 224px;
    margin-right: 120px;
  }
}
